body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
/*  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;*/
  background-image:linear-gradient(to right,#7799E7,#4F7BDF);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.editNav{
	display: flex;
	justify-content: flex-end;
	font-size: 120%;
	-webkit-text-decoration: underline black;
	        text-decoration: underline black;
	color: black;
	padding: 1%;
}

.editNavcontent:hover{
	color: #D50000;
	cursor: pointer;
	-webkit-text-decoration: underline #D50000;
	        text-decoration: underline #D50000;
}
.editLogo{
	border: 1px solid transparent;
	display: flex;
	padding: 1%;
	padding-left: 2%;
	justify-content: flex-start;
	flex-wrap: wrap;
}

.editLogocontent{
	padding: 2%;
	border: 1px solid #4F7BDF;
	display: inline-block;
	box-shadow: 1px 1px 2px #4F7BDF;
	transition-duration: all 1s;
}

.editLogocontent:hover{
	-webkit-transform: scale(1.1);
	        transform: scale(1.1);
}

.outline{
	padding:5%;
	border: 1px solid transparent;
	display :flex;
	justify-content: center;
	text-align: center;
}

.signin{
  font-size: 300%;
  font-family:'Brush Script MT','times new roman' ;
}

.form{
	padding: 2%;
	border: 1px solid rgba(6, 60, 237, 0.5);
	width: 30%;
	line-height: 400%;
	box-shadow: 2px 2px 3px rgba(6, 43, 164, 0.5);
}

.username_Password_text_Box{
	border: 1px solid transparent;
	display:inline-flex;
	font-weight: bold;
}

.username_Password{
	border: 1px solid transparent;
	width:40%;
}

.text_box{
	border: 1px solid transparent;
	width:60%;
}

.inputEdit{
	padding: 1.2%;
	width: 120%;
	border: 2px solid black;
	background-color: transparent;
}

.buttonedit{
	width:15%;
	padding:1%;
	background-color: transparent;
	border: 2px solid black;
	font-weight: bold;
}

.editRegister{
 	display: flex;
 	justify-content: center;
}

.register{
	text-decoration: underline;
	
}

.register:hover{
	cursor: pointer;
}

.imageEdit{
	display: flex;
	justify-content: center;
	/*border: 1px solid red;*/
}

#testImage{
	padding: 1%;
	width: 500px;
	height: 400px;
}

.editBox{
	padding: 1%;
	position: absolute;
	/*border: 1px solid transparent;*/
	width:500px;
	height:400px;
	display:flex;
	flex-wrap: wrap;
	justify-content: center;
}

.theBox{
	position: absolute;
	/*border:1px solid red;*/
	display:inline-flex;
	flex-wrap: wrap;
	justify-content: center;
	box-shadow: inset 0 0 0 3px red;
}
.editParticles{
  position: fixed;
  top: 0;
  bottom:0;
  left:0;
  right:0;
  z-index: -1;
}
.editInputForm{
	border: 1px solid transparent;
	color: white;
}

.header{
	border: 1px solid transparent;
	text-align: center;
	font-family: 'dancing script' , 'courier new';
	font-size: 150%;
}

.paragraph{
	text-align: center;
	font-family: 'courier new' , 'times new roman';
	font-size: 75%;
}

.manageContent{
	display: flex;
	justify-content: center;
}

.inputFormContent {
	padding: 1.5%;
	border: 1px solid #4F7BDF;
	display: inline-flex;
	width: 40%;
	background-color:#269;
	background-image: linear-gradient(white 2px, transparent 2px),
	linear-gradient(90deg, white 2px, transparent 2px),
	linear-gradient(rgba(255,255,255,.3) 1px, transparent 1px),
	linear-gradient(90deg, rgba(255,255,255,.3) 1px, transparent 1px);
	background-size: 100px 100px, 100px 100px, 20px 20px, 20px 20px;
	background-position:-2px -2px, -2px -2px, -1px -1px, -1px -1px;
	border-radius: 7px 7px 7px 7px;
	box-shadow: 1px 1px 2px #22008E;
}

.detectInput {
	padding: 5px;
	width: 70%;
	border: none;
	border-radius: 5px 5px 5px 5px;
}

.detectButton{
	width: 30%;
	background-color: yellow;
	border: none;
	border-radius: 5px 5px 5px 5px;
	cursor: pointer;
}

#para{
	color: black;
}
