.outline{
	padding:5%;
	border: 1px solid transparent;
	display :flex;
	justify-content: center;
	text-align: center;
}

.signin{
  font-size: 300%;
  font-family:'Brush Script MT','times new roman' ;
}

.form{
	padding: 2%;
	border: 1px solid rgba(6, 60, 237, 0.5);
	width: 30%;
	line-height: 400%;
	box-shadow: 2px 2px 3px rgba(6, 43, 164, 0.5);
}

.username_Password_text_Box{
	border: 1px solid transparent;
	display:inline-flex;
	font-weight: bold;
}

.username_Password{
	border: 1px solid transparent;
	width:40%;
}

.text_box{
	border: 1px solid transparent;
	width:60%;
}

.inputEdit{
	padding: 1.2%;
	width: 120%;
	border: 2px solid black;
	background-color: transparent;
}

.buttonedit{
	width:15%;
	padding:1%;
	background-color: transparent;
	border: 2px solid black;
	font-weight: bold;
}

.editRegister{
 	display: flex;
 	justify-content: center;
}

.register{
	text-decoration: underline;
	
}

.register:hover{
	cursor: pointer;
}
