.editLogo{
	border: 1px solid transparent;
	display: flex;
	padding: 1%;
	padding-left: 2%;
	justify-content: flex-start;
	flex-wrap: wrap;
}

.editLogocontent{
	padding: 2%;
	border: 1px solid #4F7BDF;
	display: inline-block;
	box-shadow: 1px 1px 2px #4F7BDF;
	transition-duration: all 1s;
}

.editLogocontent:hover{
	transform: scale(1.1);
}
